import React, { useState, useEffect } from "react";
import { useLocation, Link} from "react-router-dom";
import { 
    createUserWithEmailAndPassword, signInWithPopup, 
    GoogleAuthProvider,  GithubAuthProvider } from "firebase/auth";
// import { auth } from "../Firebase";
import styled from 'styled-components';
import { BiLogInCircle } from "react-icons/bi";
import { FcGoogle } from "react-icons/fc";
import { AiFillGithub } from "react-icons/ai";
import { authService } from "../Firebase";
// import { firebaseInstance } from "../Firebase";


const JoinBlock = styled.div`
    border-top: 96px solid #ff385c;
    text-align: center; margin-bottom: 200px;
    h3 {text-align: center; padding: 70px 0 10px; font-size: 28px}
    .joinbox { 
        border: 1px solid #555; padding-bottom: 200px;
        min-width: 560px; display: inline-block;
        padding-top: 30px; border-radius: 10px;
        p {
            line-height: 0.3; text-align: left;
            padding-left: 75px;
        }
        input { padding: 10px; min-width: 390px;
                outline: none;
            &:nth-of-type(1) {margin-bottom: 15px}
            }
        button {display: block; padding: 11px;
                min-width: 410px;
                border-radius: 4px; font-size: 16px;
            }
        .rest { 
            margin: 45px auto 10px; color: #fff;  background: #ff385c;
            transition: all 0.5s;
            &:hover {background: #e32f50}
        }
        .etclog {
            background: #ddd; border:1px solid #ccc; color: #333; margin: 20px auto 0;
            transition: all 0.5s;
            &:hover {background: #ccc}
            }
        .btnlog {
            margin-left: 275px;
            margin-top: 40px; display: inline-block;
            transition: all 0.3s;
            &:hover {border-bottom: 1px solid #555}
        }
}        
`;

const Joinsection = () => {
    const [registerEmail, setRegisterEmail] = useState("");
    const [registerPassword, setRegisterPassword] = useState("");

    const register = async () => {
        try {
            const user = await createUserWithEmailAndPassword(
                authService,
                registerEmail,
                registerPassword
            );
            console.log(user);
            console.log('회원가입이 완료되었습니다.');
        } catch (error) {
            console.log(error.message);
            console.log('회원가입을 할 수 없습니다.');
        }
    };

    const onSocialLog = async ({ target }) => {
        const name = target.closest('button').name;
        if (name === 'google') {
          await signInWithPopup(authService,
            new GoogleAuthProvider(),
          );
          console.log('구글 로그인이 완료되었습니다.');
        } else if (name === 'github') {
            await signInWithPopup(authService,
            new GithubAuthProvider(),
          );
          console.log('깃허브 로그인이 완료되었습니다.');
        }
      };

    //   const navigate = useNavigate('');

    //   const onFinish = async ({ email, password}) => {
    //     try {
    //       await authService.createUserWithEmailAndPassword(email, password);
    //       navigate("/home");
    //     } catch (error) {
    //       alert(error.message);
    //     }
    //   };

    const { pathname } = useLocation();
    useEffect(() => {
        window.scrollTo(0, 0);
    }, [pathname]);


    return (
        <JoinBlock>
            <h3>에어비앤비에 오신 것을 환영합니다.</h3>
            <div className="joinbox " >
                <p>이메일</p>
                <input placeholder="Email을 입력하세요. (@를 사용하여주세요.)" onChange={(e) => {
                        setRegisterEmail(e.target.value);
                        }} />
                <p>비밀번호</p>
                <input placeholder="Password를 입력하세요. (6자리 이상)" type="password" onChange={(e) => {
                        setRegisterPassword(e.target.value);
                        }} />
                <button onClick={register} className="rest">회원가입</button>
                <button className="etclog" onClick={onSocialLog} name="google"><FcGoogle style={{fontSize: '20px', verticalAlign: 'sub'}}/> Google 로그인</button>
                <button className="etclog" onClick={onSocialLog} name="github"><AiFillGithub style={{fontSize: '20px', verticalAlign: 'sub'}}/> Github 로그인</button>
                <Link to="/login" className="btnlog"><BiLogInCircle  style={{verticalAlign:'middle', paddingRight:'5px'}} />로그인 하러 가기</Link>
           </div>
        </JoinBlock>
    );
};

export default Joinsection;