import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { useLocation } from "react-router-dom";
import help from './help.json';
import { BiSearchAlt2 } from "react-icons/bi";
import { IoIosArrowDown } from "react-icons/io"

const HelpBlock = styled.div`
    border-top: 96px solid #ff385c;  min-height: 85vh;
    .topbox { margin-bottom: 100px; 
        h1 {margin: 50px 30px 30px; text-align: center}
        .hesearchbox { 
            display: flex; border: 1px solid #ccc; background: #fff; 
            padding: 8px 0; 
            max-width: 400px; margin: 10px auto 80px; border-radius: 50px; 
            .hesmallbox {
                color: #000; width: 80%; 
                padding: 11px 0 0 20px;
                input {border: none; outline: none; font-size: 18px}
            }
            .heboxx {padding-right: 8px;
                button {
                    font-size: 18px; padding: 4px 0 0 ;
                    background: rgb(255, 56, 92); width: 40px; height: 40px; 
                    border-radius: 50%; color: #fff; margin: 5px 0;
                    }
                }
            } 
            .questionbox {
                margin: 40px 70px; position: relative;
                &::after {content:''; width: 82%; height: 1px; background: #ccc;
                          position: absolute; bottom: 0px; left: 50%; transform: translate(-60%);}
            h3 {font-size: 20px; line-height: 1}
            > p { line-height: 0.6; 
                &:nth-of-type(1) {
                    color: #777; padding-bottom: 20px; transition: all 0.5s;
                    &:hover {cursor: pointer; text-decoration: underline;}
                }
            }
                .hidcont {
                    padding: 5px 25px 25px; width: 80%; 
                    p {line-height: 1.5}
                }
            }
    }
    
`;


const Helpsection = () => {
    const [searchBox, setSearchBox] = useState('');
    const [state, setState] = useState([]);

    const handleOpen = ({ target: { id: _id } }) => {
      const id = Number(_id);
  
      if (state.includes(id)) {
        return setState(state.filter((v) => v !== id));
      }
      setState([...state, id]);
    };

    const { pathname } = useLocation();
    useEffect(() => {
        window.scrollTo(0, 0);
    }, [pathname]);

    return (
        <HelpBlock>
         <div className='topbox'>
            <h1>무엇을 도와드릴까요?</h1>
            <div className='hesearchbox' >
                    <div className='hesmallbox'>
                       <input placeholder='검색 시작하기' type='text' onChange={(event) => {setSearchBox(event.target.value)} }></input>
                    </div>
                    <div className='heboxx'>
                        <button>
                            <BiSearchAlt2 />
                        </button>
                    </div>
            </div>
                {help.filter((val) => {
                    if (searchBox === "") {
                        return val
                    } else if (val.title.toLowerCase().includes(searchBox.toLowerCase())) {
                        return val
                    }
                }).map((val, i) => {
                        return (
                                <div className='questionbox row'>
                                    <h3>{val.title}</h3>
                                    <p onClick={handleOpen} id={i}><IoIosArrowDown style={{verticalAlign: 'middle'}} /> {val.summ}</p>
                                    {state.includes(i) && (
                                        <div className='hidcont'>
                                            <p>{val.content}</p>
                                            <p>{val.contents}</p>
                                            <p>{val.contentt}</p>
                                            <p>{val.contentf}</p>
                                            <p>{val.contentv}</p>
                                            <p>{val.contentx}</p>
                                        </div>
                                    )}
                                </div>
                            )
                    })}
         </div>
        </HelpBlock>
    );
};


export default Helpsection;